.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__contents{
    display: flex;
    justify-content: space-between;
}

.nav__black{
    background-color: #111;
}

.nav__logo{
    position: fixed;
    left: 0;
    top: 25px;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav__avatar{
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: cover;
    border: rgb(135, 135, 135) solid 1px;
}