.row{
    color: white;
    margin-left: 20px;
    margin-top: 20px;
}

.row__title{
    text-align: center;
}

.row__posters{
    display: flex;
    overflow-y:hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row__posters::-webkit-scrollbar{
    display: none;
}

.row__poster{
    height: 100px;
    margin-right: 10px;
    object-fit: contain;
    width: 100%;
    transition: transform 450ms;
}

.row__poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row__posterLarge{
    height: 250px;
}

.row__posterLarge:hover{
    transform: scale(1.09);
    opacity: 1;
}

@media only screen and (max-width: 490px) {
    .row__poster{
        height: 50px;
    }
    .row{
        margin-top: 2.4rem;
    }
  }