.banner{
    position: relative;
    height: 448px;
    color: white;
    object-fit: contain;
}

.banner__contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description{
    width: 45rem;
    font-size: 0.8rem;
    line-height: 1.3;
    padding-top: 1rem;
    max-width: 360px;
    height: 80px;
}

.banner--fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, 
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
}

.banner__buttons{
    display: flex;
    align-items: center;
}

.banner__button{
    cursor:pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51, 0.5);
}

.banner__button:hover{
    color:#000;
    background-color: #e6e6e6;
    transition: all 0.3s;
}

@media only screen and (max-width: 490px) {
    .banner__title{
        font-size: 20px;
    }
    .banner__button{
        font-size: x-small;
    }
    .banner__description{
        font-size: xx-small;
        width: 80%;
    }
    .banner{
        height: 300px;
    }
    .banner--fadeBottom{
        height: 0.4rem;
        background-image: linear-gradient(
            180deg, 
            transparent,
            #111
        );
    }
  }